.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.gallery-section {
  padding: 0;
  height: 100%;
  background-image: linear-gradient(to bottom, #fff5f6 50%, #ffffff 50%);
}

.gallery-section__pics {
  margin-left: 137px;
  margin-right: 137px;

  display: flex;
  flex-direction: row;
  align-items: center;
}

.gallery-section__position {
  &:not(:last-child) {
    margin-right: 6px;
  }
}

.gallery-section__position--down {
  padding-top: 40px;
}

.gallery-section__position--up {
  padding-bottom: 30px;
}

.gallery-section__position--last {
  padding-top: 20px;
}

.gallery-section__item {
  border-radius: 20px;
  margin-bottom: 6px;
  overflow: hidden;

  box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14),
    0px 1px 3px 0px rgba(0, 0, 0, 0.12);

  &:hover {
    transform: scale(1.5);
    transition-duration: 1500ms;
  }
}
